<template>
	<div class="UserControl">
	<!--  -->
	   <el-row :gutter="20">
		  <el-col :xs="3" :sm="3" :md="4"><el-input v-model="seach.name" placeholder=""></el-input></el-col>
		  <el-col :xs="3" :sm="3" :md="4"><el-input v-model="seach.email" placeholder=""></el-input></el-col>
		  <el-col :xs="4" :sm="3" :md="4">
		    <el-select v-model="seach.departmentId" placeholder="">
		      <!-- <el-option label="" value=""></el-option> -->
		      <el-option label="" value="0"></el-option>
		      <el-option label="" value="7"></el-option>
		      <el-option label="" value="5"></el-option>
		      <el-option label="" value="6"></el-option>
		    </el-select>
		  </el-col>
		  <el-col :xs="4" :sm="3" :md="4">
		  	<el-select v-model="seach.level" placeholder="">
		      <!-- <el-option label="" value=""></el-option> -->
		      <el-option label="" value="0"></el-option>
		      <el-option label="" value="1"></el-option>
		      <el-option label="" value="2"></el-option>
		      <el-option label="" value="3"></el-option>
		    </el-select>
		  </el-col>
		   <el-col :xs="10" :sm="8" :md="6" >
		   	 <el-button  type="primary" icon="el-icon-search" @click="secrch" size="small"></el-button>
		   	 <el-button  type="success" icon="el-icon-plus" @click="addUser" size="small" v-show="isShow"></el-button>
		   </el-col>
		</el-row>
		<!--  -->
		<div class="table-wrapper">
			<el-table :data="fbsData" style="width: 90%" border size="small"  v-loading="loading" element-loading-text="...">
                <el-table-column prop="name" label="" width='80'></el-table-column>
                <el-table-column prop="email" label="" width="200"></el-table-column>
                <el-table-column prop="departmentId" label=""  ></el-table-column>
                <el-table-column prop="level" label=""></el-table-column>
                <el-table-column prop="date" label="" width="150"></el-table-column>
               <!--  <el-table-column prop="showTitle" label="" v-if="isShow">
		            <template slot-scope="scope">
		                <el-button close-transition @click="adminuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow"></el-button>
		            </template>   
		      </el-table-column> -->
               <!--  <el-table-column label="" width="100" v-if="isShow">
                    <template slot-scope="scope">
                        <el-button @click="readonlyuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow && scope.row.state == 0"><i class="fa fa-ban"></i></el-button>
                        <el-button @click="readonlyuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow && scope.row.state != 0"><i class="fa fa-ban staylcolor"></i></el-button>
                        <el-button @click="deleteuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow"><i class="el-icon-delete"></i></el-button>
                    </template>
                </el-table-column> -->
            </el-table>
            <el-pagination
                background
                @size-change="pageSizeChange"
                @current-change="currentPageChange"
                :current-page="currentPage"
                :page-sizes="pageSizes"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="dataSize" class="paginationWrapper" v-show="tableshow">
            </el-pagination>
		</div>
		<!--  -->
		<el-dialog title="" :visible.sync="dialogVisible" width="50%" >
            <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
			  <el-form-item label="()" prop="email">
			    <el-input v-model="ruleForm.email"></el-input>
			  </el-form-item>
			  <el-form-item label="" prop="pwd">
			    <el-input v-model="ruleForm.pwd"></el-input>
			  </el-form-item>
			  <el-form-item label="" prop="name">
			    <el-input v-model="ruleForm.name"></el-input>
			  </el-form-item>
			  <el-form-item label="" prop="departmentId">
			    <el-select v-model="ruleForm.departmentId" placeholder="">
			      <div v-for="item in userdepartment">
			          <el-option :label="item.name" :value="item.id"></el-option>
			     </div>
			    </el-select>
			  </el-form-item>
			  <el-form-item label="" prop="level">
			    <el-select v-model="ruleForm.level" placeholder="">
			      <div v-for="item in userlevel">
			          <el-option :label="item.name" :value="item.id"></el-option>
			      </div>
			    </el-select>
			  </el-form-item>
			  <el-form-item label="" prop="higherId">
			    <el-select v-model="ruleForm.higherId" placeholder="">
			    	<div v-for="item in superior">
			             <el-option :label="item.name" :value="item.id"></el-option>
			    	</div>
			      <!-- <el-option label="" value="beijing"></el-option> -->
			    </el-select>
			  </el-form-item>
			   <el-form-item>
			    <el-button type="primary" @click="submitForm('ruleForm')" :loading="addSaveLoading"></el-button>
			    <el-button @click="dialogVisible = false"></el-button>
			  </el-form-item>
			</el-form>
		</el-dialog>
	   <!--  -->
	</div>
</template>
<script>
import {getAdminList,addAdmin,AdminParameter} from '../../api/api.js'
	export default{
		data(){
			return{
				// 
				isShow: true,
				// 
				tableshow: false,
				loading: false,
				// 
				userpermission:null,
				// 
				userperadmin:{},
				dialogVisible: false,
				// 
				userlevel:[],
				// 
				superior:[],
				// 
				userdepartment:[],
				// 
				seach:{
				  sessionid:'',
                  name: '',
				  email: '',
				  departmentId:'',
				  level:'',
				  pageId:0,
				  pageSize:10,
				  first:0
				},
				// 
				currentPage: 1,
			    pageSizes: [10,20,50],
			    dataSize: 0,
			    pagesize: 10,
			    // 
				fbsData:[
				  
				],
				// 
                 ruleForm: {
                  sessionid:'',
		          email:'',
		          pwd: '',
		          name:'',
		          departmentId:'',
		          level: '',
		          higherId:''

		        },
		        addSaveLoading:false
			}
		},
		mounted(){
			// 
			// this.getUserInfo();
			this.seach.sessionid = parseInt(localStorage.getItem("userId"));
			this.userpermission = parseInt(localStorage.getItem("userpermission"));
			var userperadmin = localStorage.getItem("userperadmin");
			this.userperadmin = JSON.parse(userperadmin);
			// console.log(this.userperadmin.sessionid)
			this.ruleForm.sessionid = this.userperadmin.sessionid;
			if (this.userpermission >2) {
                this.isShow = false;
			} else {
				this.isShow = true;
			}
			this.secrch();
		},
		 methods: {
		 	// 
		  secrch(){
		  	console.log(this.seach)
		  	if (this.seach.email) {
		  		var reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/; 
                if (!reg.test(this.seach.email)) {
                    this.$message.error('');
                    return;
                }
		  	}
		  	if (this.seach.departmentId === "0") {
		  		this.seach.departmentId = "";
		  	} 
		  	 if (this.seach.level === "0") {
		  		this.seach.level = "";
		  	}
		  	this.loading = true;
		  	let seach = JSON.stringify(this.seach)
		  	console.log(seach)
		  	getAdminList(seach).then((res)=>{
		  		console.log(res.data)
		  		if (res.data.status === 0) {
		  			// 
		  			this.fbsData = res.data.adminList;
		  			if (this.fbsData.length<1) {
		  				this.loading = false;
                        // 
			  			this.tableshow = false;
		  			} else {
		  				for (var i = 0; i <this.fbsData.length; i++) {
			  				// 
							this.fbsData[i].date = this.formatDate(this.fbsData[i].date);
			  				if (this.fbsData[i].departmentId === 5) {
			  					this.fbsData[i].departmentId = ""
			  				} else if (this.fbsData[i].departmentId === 6) {
			  					this.fbsData[i].departmentId = ""
			  				} else if (this.fbsData[i].departmentId === 7) {
			  					this.fbsData[i].departmentId = ""
			  				};
			  				if (this.fbsData[i].level === 1) {
			  					this.fbsData[i].level = ''
			  				} else if (this.fbsData[i].level === 2) {
			  					this.fbsData[i].level = ''
			  				} else if (this.fbsData[i].level === 3) {
			  					this.fbsData[i].level = ''
			  				};
			  			}
			  			if (!res.data.listSize) {
			  				this.dataSize = this.dataSize;
			  			} else {
			  			   this.dataSize= parseFloat(res.data.listSize);
			  			}
			  			this.loading = false;
			  			// 
			  			this.tableshow = true;
			  		}	
		  		} else if (res.data.status === 1) {
		  		   this.tableshow = false;
		  		   this.$router.replace("/login");
		  		}
		  	}).catch(res=>{
		  		this.loading = false;
		  		this.$message.error('');
		  		console.log(res)
		  	})
		  },
		  // 
		  addUser(){
		  	// 
		        this.ruleForm.email = '';
		        this.ruleForm.pwd = '';
		        this.ruleForm.name = '';
		        this.ruleForm.departmentId = '';
		        this.ruleForm.level = '';
		        this.ruleForm.higherId = '';
		    // 
		  	this.userdepartment = [];
		  	if (this.userperadmin.admin.departmentId === 5) {
				this.userdepartment.push({'name':'','id':5});
			} else if (this.userperadmin.admin.departmentId === 6) {
				this.userdepartment.push({'name':'','id':6});
			} else if (this.userperadmin.admin.departmentId === 7) {
				this.userdepartment.push({'name':'','id':7});
			};
			// 
			this.userlevel = [];
		  	console.log(this.userperadmin.admin.level)
		  	if (this.userperadmin.admin.level === 1) {
		  		this.userlevel = [{'name':'','id':2},{'name':'','id':3}];
		  	} else  {
		  		this.userlevel = [{'name':'','id':3}];
		  	};
		  	var data = {
		  		sessionid : this.userperadmin.sessionid
		  	};
		  	data = JSON.stringify(data);
		  	AdminParameter(data).then(res=>{
                console.log(res.data)
                if (res.data.status === 0) {
                	this.superior = res.data.adminList;
                	console.log(this.superior)
                	this.dialogVisible = true;
                }
                if (res.data.status === 1) {
                  this.$router.replace("/login");
                }
		  	}).catch(res=>{
		  		this.$message.error('');
		  	})
		  	// this.userperadmin;
		  	// this.dialogVisible = true;
		  },
		  pageSizeChange(res){
		  	this.seach.pageSize = res;
		  	this.seach.first = 1;
		  	this.secrch()
		  },
		  currentPageChange(res){
		  	this.seach.pageId = (res*1-1);
		  	this.seach.first = 1;
		  	this.secrch()
		  },
		  // 
		  adminuser(data,index){
		  	console.log(data)
		  	console.log(index)
		  },
		  // 
		  readonlyuser(data,index){
		  	console.log(data)
		  	console.log(index)
		  },
		  // 
		  deleteuser(data,index){},
	      submitForm(formName) {
	        this.$refs[formName].validate((valid) => {
	          if (valid) {
	            console.log(this.ruleForm)
	            if (this.ruleForm.email === '') {
	            	this.$message.error('');
	            	
	            } else if (this.ruleForm.pwd === '') {
	            	this.$message.error('');
	            } else if (this.ruleForm.name === '') {
	            	this.$message.error('');
	            } 
	            else if (this.ruleForm.departmentId === '') {
	            	this.$message.error('');
	            } else if (this.ruleForm.level === '') {
	            	this.$message.error('');
	            } 
	            else if (this.ruleForm.higherId === '') {
	            	this.$message.error('');
	            } else {
	            	if (this.ruleForm.email) {
	            	    var reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/; 
		                if (!reg.test(this.ruleForm.email)) {
		                    this.$message.error('');
		                    return;
		                } else {
		                	this.addSaveLoading = true;
		                	var _this = this;
		                	let ruleForm = JSON.stringify(_this.ruleForm)
		                	addAdmin(ruleForm).then(res=>{
                               console.log(res.data)
                               if (res.data.status === 0) {
                               	   _this.$message({
							          message: res.data.statusMsg,
							          type: 'success'
							        });
                               	   _this.addSaveLoading = false;
                               	   _this.dialogVisible = false;
                               	   _this.secrch();
                               }
                               if (res.data.status === 1) {
                               	 _this.$router.replace("/login");
                               	 _this.addSaveLoading = false;
                               	 _this.dialogVisible = false;
                               }
		                	}).catch(res=>{
                               _this.addSaveLoading = false;
                               _this.dialogVisible = false;
                               _this.$message.error('');
		                	})
		                }
	            	}
	            }
	          } else {
	            console.log('error submit!!');
	            return false;
	          }
	        });
	      }
	    }
	}
</script>
<style lang="scss" type="text/css" scoped>
   .UserControl{margin-top: 1.25rem;}
   .table-wrapper{margin-top: 1.5rem;}
   .staylcolor{color:red}
</style>


